import axios from "axios";

export const getProducts = (query = "") => {
  return axios
    .get(`${process.env.REACT_APP_HOST_URL}/api/products/findAll?s=${query}`)
    .then((response) => {
      return response;
    })
    .catch((error) => {
      return { error };
    });
};
