import React from "react";
import { useSelector } from "react-redux";

import Header from "../Header";
import Container from "../Container";
import Grid from "../Grid";

import { cartItems as _cartItems } from "../../features/cart/cartSlice";

export default function Cart() {
  const cartItems = useSelector(_cartItems);

  React.useEffect(() => {
    console.log(cartItems);
  });

  return (
    <>
      <Header />
      <Container>
        {!cartItems ? "Нет данных" : <Grid data={cartItems} type="cart" />}
      </Container>
    </>
  );
}
