import axios from "axios";

export const login = (email, password) => {
  if (!email || !password) return;

  return axios
    .post(`${process.env.REACT_APP_HOST_URL}/api/auth/login`, {
      email,
      password,
    })
    .then((response) => {
      return response;
    })
    .catch((error) => {
      return {error};
    });
};
export const logout = () => {};
export const checkUser = () => {};
