import React from "react";
import { createUseStyles } from "react-jss";
import { Card, Typography, Button } from "antd";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  cartItems as _cartItems,
  pushToCart as _pushToCart,
  popFromCart as _popFromCart,
} from "../../features/cart/cartSlice";
import { isAuthenticated as _isAuthenticated } from "../../features/auth/authSlice";

const { Meta } = Card;
const { Title } = Typography;

const useStyles = createUseStyles({
  cardWrapper: {
    margin: "5px",
    width: 240,
    // height: 350,
  },
  cardImg: {
    height: "200px",
    objectFit: "contain",
  },
  cardPrice: {
    marginTop: "5px",
  },
  cardTitle: {
    textOverflow: "clip",
  },
});

export default function CustomCard({ data, type }) {
  const history = useHistory();
  const classes = useStyles();
  const cartItems = useSelector(_cartItems);
  const isAuthenticated = useSelector(_isAuthenticated);
  const dispatch = useDispatch();

  const handleCardClick = (event) => {
    history.push(`/catalog/${data.id}`);
  };

  const handleButtonClick = (event) => {
    event.stopPropagation();
    if (type === 'catalog') {
      dispatch(_pushToCart(data));
    }
    if (type === 'cart') {
      dispatch(_popFromCart(data));
    }
  };

  return (
    <Card
      hoverable
      className={classes.cardWrapper}
      cover={
        <img alt="card image" src={data.imageUrl} className={classes.cardImg} />
      }
      onClick={handleCardClick}
    >
      <Meta
        className={classes.cardTitle}
        title={data.title}
        description={data.description}
      />
      <Title className={classes.cardPrice} level={4}>
        Цена: {data.price}
      </Title>
      {isAuthenticated && (
        <>
          {cartItems.find((item) => item.id == data.id) !== undefined ? (
            <Button type="primary" onClick={handleButtonClick} disabled>
              Уже в корзине
            </Button>
          ) : (
            <Button type="primary" onClick={handleButtonClick}>
              {type === "catalog" && "В корзину"}
              {type === "cart" && "Удалить"}
            </Button>
          )}
        </>
      )}
    </Card>
  );
}
