import React from "react";
import PropTypes from "prop-types";
import { Input, PageHeader, Button, Avatar } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";

import { isAuthenticated as _isAuthenticated } from "../../features/auth/authSlice";
import {
  catalogItems as _catalogItems,
  getCatalog,
} from "../../features/catalog/catalogSlice";

import logo from "../../assets/logo.svg";
import profile from "../../assets/profile.svg";

const { Search } = Input;

export default function Header() {
  const isAuthenticated = useSelector(_isAuthenticated);
  const dispatch = useDispatch();

  const handleSearch = (value) => {
    dispatch(getCatalog(value));
  };

  return (
    <PageHeader
      title={<Link to="/">Bookify</Link>}
      // className="site-page-header"
      subTitle="Книги есть"
      extra={[
        <Search
          placeholder="Поиск"
          onSearch={(value) => handleSearch(value)}
          style={{ width: 200, marginRight: "auto" }}
        />,
        <span key="1">
          {isAuthenticated ? (
            <>
              <Link to="/cart">
                <Button type="primary">Корзина</Button>
              </Link>
              <Link to="/profile" key="2">
                <Avatar src={profile} />
              </Link>
            </>
          ) : (
            <Link to="/login">
              <Button type="primary">Войти</Button>
            </Link>
          )}
        </span>,
      ]}
      avatar={{
        src: logo,
      }}
      // breadcrumb={{ routes }}
    ></PageHeader>
  );
}

Header.propTypes = {
  routes: PropTypes.array,
};
