import { createSlice } from "@reduxjs/toolkit";

export const cartSlice = createSlice({
  name: "cart",
  initialState: {
    items: [],
  },
  reducers: {
    addItem: (state, action) => {
      state.items = [...state.items, action.payload.item];
    },
    removeItem: (state, action) => {
      state.items = state.items.filter((item) => item === action.payload.id);
    },
  },
});

const { removeItem, addItem } = cartSlice.actions;

export const pushToCart = (item) => async (dispatch) => {
  if (item.hasOwnProperty("id") && item.hasOwnProperty("title")) {
    dispatch(addItem({ item }));
  }
};

export const popFromCart = (itemId) => async (dispatch) => {
  if (itemId instanceof Number) {
    dispatch(removeItem({ id: itemId }));
  }
};

export const cartItems = (state) => state.cart.items;

export default cartSlice.reducer;
