import { createSlice } from "@reduxjs/toolkit";
import { getProducts } from "../../api/catalog";

export const catalogSlice = createSlice({
  name: "catalog",
  initialState: {
    items: [],
    query: "",
    selected: null,
  },
  reducers: {
    setItems: (state, action) => {
      state.items = action.payload.items;
    },
    setSelected: (state, action) => {
      state.selected = action.payload.item;
    },
    setQuery: (state, action) => {
      state.query = action.payload.query;
    },
  },
});

const { setItems, setQuery, setSelected } = catalogSlice.actions;

export const getCatalog = (query = "") => async (dispatch) => {
  if (typeof query === "string") {
    const response = await getProducts(query);

    if (response.error) {
      console.log(response.error);
      return;
    }

    dispatch(setItems({ items: response.data }));
    dispatch(setQuery({ query }));
  }
};

export const setSelectedItem = (item) => async (dispatch) => {
  dispatch(setSelected({ item }));
};

// export const setCatalogQuery = (query) => async (dispatch) => {
//   if (typeof query === "string") {
//     dispatch(setQuery({ query }));
//   }
// };

export const catalogSelected = (state) => state.catalog.selected;
export const catalogItems = (state) => state.catalog.items;
export const catalogQuery = (state) => state.catalog.query;

export default catalogSlice.reducer;
