import React from "react";

import { useParams } from "react-router-dom";
import { Typography, Button } from "antd";
import { useSelector } from "react-redux";
import Header from "../Header";
import Container from "../Container";
import { catalogItems as _catalogItems } from "../../features/catalog/catalogSlice";
import { isAuthenticated as _isAuthenticated } from "../../features/auth/authSlice";

const { Title, Text } = Typography;

export default function Product() {
  const [selected, setSelected] = React.useState(null);
  const { id } = useParams();

  const catalog = useSelector(_catalogItems);
  const isAuthenticated = useSelector(_isAuthenticated);

  React.useEffect(() => {
    setSelected(catalog.find((item) => item.id == id));
  }, []);

  return (
    <>
      <Header />
      <Container>
        {selected && (
          <>
            <img src={selected.imageUrl}/>
            <div>
              <Title level={4}>Наименование</Title>
              <Text>{selected.title}</Text>
              <Title level={4}>Описание</Title>
              <Text>{selected.description}</Text>
              <Title level={4}>Цена</Title>
              <Text>{selected.price}</Text>
            </div>
            {isAuthenticated && <Button type="primary">В корзину</Button>}
          </>
        )}
      </Container>
    </>
  );
}
